	components: {
		uniFav
	},
	props: {
		users:{
			type: Array,
			default: () => []
		},
		navBarName: {
			type: String,
			default: 'WeTeam'
		},
		ids:{
			type: Array,
			default: () => []
		},
	},
	computed: {
		...mapState(['hasLogin', 'userInfo']),
	},
	data() {
		return {
			favContext:{
				contentDefault: '关注',
				contentFav:'已关注'
			}
		}
	},
	methods: {
		collect(id){
			this.$emit("follow", id)
		}
	}
